import React from 'react';
import './header.css';
import { Divider } from '@mui/material';
import logo from '../../assets/TurboExp-Logo-Black.png';
import { GetStarted } from '../../components';
import InfoIcon from '@mui/icons-material/Info';
import ThumbUpIcon from '@mui/icons-material/ThumbUp';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';

const Header = () => {
  return (
    <>
    {/* Logo */}
      <div className='logo-border section__padding'>
        <h1 className='gradient__txt'>
              Unleash the Power of Swift Logistics - Connecting You to Reliable and Lightning-Fast Deliveries!
        </h1>

        <div className='logo-img'>
          <img src={logo} alt="Turbo Express" />
        </div>
      </div>

      
      {/* Header-Section */}
        <div className='head-section section__padding' id='home'>

          <div className='head-content'>

            <Divider sx={{ background: '#1e2636' }} flexItem />

            {/* Cards */}
            <div className='card-container container_wrap'>

                <div className='card-head'>
                  <div className='card-title'>
                    <ThumbUpIcon />
                  </div>
                  <div className='card-details'>
                    <h2>Our Promise</h2>
                    <p>We ensure secure, prompt deliveries for critical documents, cherished gifts, and essential supplies, making us your trusted partner for hassle-free logistics.</p>
                  </div>
                </div>

                <div className='card-head'>
                  <div className='card-title'>
                    <CheckCircleIcon />
                  </div>
                  <div className='card-details'>
                    <h2>Why Choose Turbo Express</h2>
                    <p>Our unwavering commitment to excellence and customer satisfaction sets us apart as the preferred choice for all your delivery needs.</p>
                  </div>
                </div>

                <div className='card-head'>
                  <div className='card-title'>
                    <InfoIcon />
                  </div>
                  <div className='card-details'>
                    <h2>About Turbo Express</h2>
                    <p>Turbo Express is a top logistics and courier service known for fast and dependable delivery solutions.</p>
                  </div>
                </div>

              </div>

              <Divider sx={{ background: '#1e2636' }} flexItem />

            <div className='head-content_btn'>
              <GetStarted />
            </div>

          </div>
        </div>

    </>
  )
}

export default Header