import React from 'react'
import './app.css'
import { Navbar } from './components'
import { Header, Footer } from './containers'

const App = () => {
  return (
    <div className='body_app'>
        <div className='head_nav'>
            <Navbar />
        </div>
        
        <Header />
        <Footer />
    </div>
  )
}

export default App